import React from 'react';
import Medal from './Medal';
import Card from 'react-bootstrap/Card';
import Badge from 'react-bootstrap/Badge';
import ListGroup from 'react-bootstrap/ListGroup';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import { TrashFill, XCircle, Floppy } from 'react-bootstrap-icons';

const Country = (props) => {
    const { country, medals, onIncrement, onDecrement, onDelete, onSave, onReset } = props;
    const getMedalsTotal = (country, medals) => {
        let sum = 0;
        medals.forEach(medal => { sum += country[medal.name].page_value; });
        return sum;
    }
    const renderSaveButton = () => {
        let unsaved = false;
        medals.forEach(medal => {
            if (country[medal.name].page_value !== country[medal.name].saved_value) {
                unsaved = true;
            }
        });
        return unsaved;
    }
    const renderCancelTooltip = (props) => (
        <Tooltip id="save-tooltip" {...props}>
            Discard Changes
        </Tooltip>
    );
    const renderSaveTooltip = (props) => (
        <Tooltip id="save-tooltip" {...props}>
            Save Changes
        </Tooltip>
    );
    return (
        <Card>
            <Card.Body>
                <Card.Title className="d-flex justify-content-between">
                    <span>
                        { country.name }
                        <Badge bg="secondary" text="light" pill className="ms-2">
                            { getMedalsTotal(country, medals) }
                        </Badge>
                    </span>
                    { renderSaveButton() ?
                        <React.Fragment>
                            <OverlayTrigger
                                placement="bottom"
                                delay={{ show: 200, hide: 350 }}
                                overlay={renderCancelTooltip}
                            >
                                <XCircle color="red" onClick={ () => onReset(country.id) } className='icon-btn ms-5' />
                            </OverlayTrigger>
                            <OverlayTrigger
                                placement="bottom"
                                delay={{ show: 200, hide: 350 }}
                                overlay={renderSaveTooltip}
                            >
                                    <Floppy color="green" onClick={ () => onSave(country.id) } className='icon-btn' />
                            </OverlayTrigger>
                        </React.Fragment>
                        :
                        <TrashFill color="red" onClick={() => onDelete(country.id)} className='icon-btn' />
                    }
                </Card.Title>
                <ListGroup variant="flush">
                    { medals.map(medal =>
                        <ListGroup.Item className="d-flex justify-content-between" key={ medal.id }>
                            <Medal  
                                country={ country } 
                                medal={ medal } 
                                onIncrement={ onIncrement } 
                                onDecrement={ onDecrement } />
                        </ListGroup.Item>
                    ) }
                </ListGroup>
            </Card.Body>
        </Card>
    );
}

export default Country;
